<template>
  <div data-app>
    <v-dialog v-model="dialog">
      <img @click="closeDialog()" :src="getImgUrl(img_src)" alt="" width="50%">
    </v-dialog>
  </div>
</template>

<script>
export default {
    name: 'DialogPhoto',
    props: {
      img: String
    },
    data: () => ({
      dialog: false,
      img_src: null
    }),
    watch: {
      img(v) {
        if (v == '' || v == null) {
          this.dialog = false;
          this.img_src = null;
          return;
        } 
        this.img_src = v;
        this.dialog = true;
      }
    },
    methods: {
      closeDialog() {
        this.$emit('close-photo-dialog');
      },
      getImgUrl(img) {
        if (img == '' || img == null) return null;

        const images = require.context('../assets/', false, /\.jpg$/);
        return images('./' + img + ".jpg");
      },
    },
    mounted() {

    }
  }
</script>