<template>
  <div>
    <v-container class="container_anim">
      <div class="photos_carousel" v-if="status == 'carousel'">
        <PhotosCarousel
          @further="completed"
          class="container_anim" 
          :PhotosData="photos"
        ></PhotosCarousel>
      </div>
      <div v-if="status == 'begin' || status == 'completed'">
        <DialogPhoto
          :img="selectedImg" 
          @close-photo-dialog="closePhoto()"
        />
      </div>
      <template v-if="status == 'begin' || status == 'completed'">
        <div style="margin-bottom: 10px">
          <div style="justify-content: center; display: flex;">
            <span style="font-family: 'Playfair Display SC', serif;">
              Моменты жизни
            </span>
          </div>
        </div>
        <v-row @click="clickPhoto($event)" id="photo_row">
          <v-col v-for="photo in photos" :key="`col_${photo.id}`" cols="4" style="width: 100%; height: 171px">
            <div style="display: flex; justify-content: center;">
              <img style="touch-action: manipulation;" :id="photo.id" :src="getImgUrl(photo.file_name)" width="110px"/>
              <v-badge v-if="photo.select_number != 0" overlap bordered color="#FF4800">
                <template v-slot:badge>
                  <span style="font-size: 14px">
                    {{ photo.select_number }}
                  </span>
                </template>
              </v-badge>
            </div>
          </v-col>
        </v-row>
        <v-tooltip v-if="status == 'begin'" top :open-on-click="true" :open-on-hover="false">
          <template v-slot:activator="{ on }">
            <v-btn
              color="primary"
              @click="on.click"
              v-on="on"
              icon
              style="position: absolute; bottom: 10px"
            >
            <v-icon>
              mdi-help
            </v-icon>
            </v-btn>
          </template>
          <div>
            Необходимо выбрать порядок фото по возрастанию даты (где 1 - самая ранняя дата, 9 - самая поздняя).
            <ul>
              <li>Однократное нажатие выбирает фото</li>
              <li>Двойное нажатие на фото позволяет просмотреть фото на полный экран</li>
            </ul>
          </div>
        </v-tooltip>
      </template>
      <template v-if="status == 'completed'">
        <div class="complete_anim" style="color: #8b21fc; font-size: 80px; font-weight: bolder; height: 80px; width: 238px; position: absolute; top: 0; bottom: 0; left: 0; right: 0; margin: auto">
          Успех!
        </div>
      </template>
    </v-container>
    <div v-show="status == 'end'" style="margin-top: 90px;" class="animation_end">
      <img :src="getPngUrl('birthday')" width="100%">
    </div>
    <Actor :message="current_msg" :emoji="current_emoji" :delay="current_delay"/>
  </div>
</template>

<script>
import DialogPhoto from './DialogPhoto.vue';
import PhotosCarousel from './PhotosCarousel.vue';
import Actor from './Actor.vue';

function stop(event) {
  event.currentTarget.acontroller.abort();
}

export default {
    name: 'TaskPhotos',
    components: {
      DialogPhoto,
      PhotosCarousel,
      Actor
    },
    data: () => ({
      current_msg: '',
      current_emoji: '',
      current_delay: 0,
      status_msgs: {
        'taskphotos_dlg1': [
          {
            text: 'Хм, задание с фотографиями...',
            delay: 2600,
            emoji: 'max_2',
          },
          {
            text: 'О, я знаю что это!',
            delay: 1600,
            emoji: 'max_7',
          },
          {
            text: 'Тебе необходимо выбрать фотографии по возрастанию даты. Для этого необходимо кликнуть на фото. Для детального просмотра фотографии необходимо нажать на нее два раза.',
            delay: 14000,
            emoji: 'max_1',
          },
          {
            text: 'У тебя получится, Любовь моя!',
            delay: 2600,
            emoji: 'max_5',
            next_status: 'begin'
          }
        ],
        'taskphotos_dlg2': [
          {
            text: 'Умничка какая, у тебя получилось!',
            delay: 2900,
            emoji: 'max_6',
          },
          {
            text: 'Подумай что там и можешь начать распаковку.',
            delay: 3850,
            emoji: 'max_1',
            next_status: 'end'
          }
        ],
      },
      status: 'taskphotos_dlg1', // taskphotos_dlg1 -> begin -> completed -> carousel -> taskphotos_dlg2 -> end
      selectedImg: null,
      dblclick_timer: null,
      click: 0,
      selected_photos: 0,
      photos: [
        {
          id: 1,
          file_name: '1',
          date: '05.04.2023',
          date_type: '2023-02-25',
          select_number: 0,
          description: 'Ждем заказ в KFC'
        },
        {
          id: 2,
          file_name: '2',
          date: '05.06.2023',
          date_type: '2023-06-05',
          select_number: 0,
          description: 'На смотровой площадке БЦ "Высоцкий" в Екатеринбурге'
        },
        {
          id: 3,
          file_name: '3',
          date: '02.07.2023',
          date_type: '2023-07-02',
          select_number: 0,
          description: 'Покушали в ТЦ "Планета"'
        },
        {
          id: 4,
          file_name: '4',
          date: '04.06.2023',
          date_type: '2023-06-04',
          select_number: 0,
          description: 'Посетили Ельцин Центр в Екатеринбурге'
        },
        {
          id: 5,
          file_name: '5',
          date: '27.04.2023',
          date_type: '2023-04-27',
          select_number: 0,
          description: 'В бургерной "Ронни"'
        },
        {
          id: 6,
          file_name: '6',
          date: '08.04.2023',
          date_type: '2023-04-08',
          select_number: 0,
          description: 'На дне рождении твоего Любимого'
        },
        {
          id: 7,
          file_name: '7',
          date: '25.02.2023',
          date_type: '2023-02-25',
          select_number: 0,
          description: 'Наша первая прогулка по Кизелу'
        },
        {
          id: 8,
          file_name: '8',
          date: '14.07.2023',
          date_type: '2023-07-14',
          select_number: 0,
          description: 'Кушаем пиццу в Додо'
        },
        {
          id: 9,
          file_name: '9',
          date: '09.05.2023',
          date_type: '2023-05-09',
          select_number: 0,
          description: 'Салют на 9 мая'
        }
      ],
      true_answers: {
        1: 2,
        2: 7,
        3: 8,
        4: 6,
        5: 4,
        6: 3,
        7: 1,
        8: 9,
        9: 5
      }
    }),
    watch: {
      photos: {
        handler(v) {
          this.checkComplete(v);
        },
        deep: true
      }
    },
    methods: {
      timeout_msg(delay) {
        return new Promise(resolve => setTimeout(resolve, delay));
      },
      timeout_click(signal) {
        return new Promise((resolve, reject)=> {
          const error = new DOMException('aborted!', 'AbortError');

          const timeout = setTimeout(() => {
            resolve('you report!'); 
          }, 600000);

          if (signal.aborted) {
            clearTimeout(timeout);
            return reject(error);//[2]
          }

          signal.addEventListener('abort',() => {
            clearTimeout(timeout);
            reject(error);
          });
        });
      },
      async startDialog() {
        const messages = this.status_msgs[this.status];
        if (messages == undefined) {
          this.current_msg = '';
          return;
        }

        const msg_elem = document.querySelector('#msg');

        for (let i = 0; i < messages.length; i++) {
          const msg = messages[i];
          console.log('удален')
          msg_elem.removeEventListener('click', stop);
          this.current_msg = msg.text;
          this.current_emoji = msg.emoji;
          this.current_delay = msg.delay;

          await this.timeout_msg(msg.delay);

          console.log('добавлен')
          let acontroller = new AbortController();
          let signal = acontroller.signal;
          msg_elem.addEventListener('click', stop);
          msg_elem.acontroller = acontroller;

          try {
            await this.timeout_click(signal);
          } catch (err) {
            // console.log();
          }

          if (i == messages.length - 1) {
            this.current_msg = '';
          }

          // eslint-disable-next-line no-prototype-builtins
          if (msg.hasOwnProperty('next_status')) {
            this.status = msg.next_status;
          }
        }
      },
      completed() {
        this.status = 'taskphotos_dlg2';
        this.startDialog();
        // this.$emit('completed');
      },
      checkComplete(photos) {
        if (this.status != 'begin') return;
        for (const photo of photos) {
          if (photo.select_number != this.true_answers[photo.id]) return;
        }
        this.status = 'completed';
        document.getElementById('photo_row').style.opacity = 0.6;
        
        setTimeout(() => {
          this.status = 'carousel';
        }, 3000);
      },
      clickPhoto(e) {
        this.click += 1;
        const target_id = e.target.getAttribute('id');
        const find_photo = this.photos.find((i) => i.id == target_id);
        if (find_photo == undefined) return;
        this.dblclick_timer = setTimeout(() => {
          if (this.click === 1) {
            this.selectPhoto(find_photo, e);
            this.click = 0;
          } else {
            clearTimeout(this.dblclick_timer);
            this.showPhoto(find_photo);
            this.click = 0;
          }
        }, 200);
      },
      closePhoto() {
        this.selectedImg = null;
      },
      showPhoto(data) {
        this.selectedImg = data.file_name;
      },
      shufflingPhotosSelectNumber(num) {
        for (const photo of this.photos) {
          if (photo.select_number > num) {
            photo.select_number--;
          }
        }
      },
      selectPhoto(data, e) {
        if (data.select_number != 0) {
          e.target.classList.remove('selected_photo');
          this.shufflingPhotosSelectNumber(data.select_number);
          data.select_number = 0;
          this.selected_photos--;
          return;
        }
        e.target.classList.add('selected_photo');
        data.select_number = this.selected_photos + 1;
        this.selected_photos++;
      },
      getPngUrl(img) {
        if (img == '') return null;

        const images = require.context('../assets/', false, /\.png$/);
        return images('./' + img + ".png");
      },
      getImgUrl(img) {
        if (img == '') return null;

        const images = require.context('../assets/', false, /\.jpg$/);
        return images('./' + img + ".jpg");
      },
    },
    mounted() {
      this.startDialog();
    }
  }
</script>

<style scoped>

.container_anim {
  animation: 5s ease alternate smooth_appearance;
}

@keyframes smooth_appearance {
  from {
    opacity: 0;
  }
  70% {
    opacity: 0.9;
  }
  to {
    opacity: 1;
  }
}
.selected_photo {
  border: 3px solid green;
}

.complete_anim {
  animation: 3s ease alternate anim_complete;
  
}

.photos_carousel {
  height: calc(100vh - 130px); 
  display: flex; 
  align-items: center;
}

@keyframes anim_complete {
  from {
    font-size: 10px;
    width: 30px;
  }
  to {
    font-size: 80px;
    width: 238px;
  }
}

.animation_end {
  animation: 2s linear end;
}

@keyframes end {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

</style>