<template>
  <div>
    <div style="display: flex; width: 100vw; overflow: scroll; gap: 10px">
      <v-card v-for="photo in sortedPhoto" :key="photo.id" class="card">
        <v-card-title style="justify-content: center;">
          {{ photo.date }}
        </v-card-title>
        <v-card-text>
          <img :src="getImgUrl(photo.file_name)" alt="" width="200px">
          <div>
            {{ photo.description }}
          </div>
        </v-card-text>
      </v-card>
      <v-card style="width: 100px; display: flex; align-items: center;">
        <v-btn @click="further()" style="height: 100%;">
          <v-icon size="40">
            mdi-arrow-right-bold
          </v-icon>
        </v-btn>
      </v-card>
    </div>
  </div>
</template>

<script>

export default {
  name: 'PhotosCarousel',
  components: {
  },
  props: {
    PhotosData: Array
  },
  data() {
    return {
    }
  },
  computed: {
    sortedPhoto() {
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      return this.PhotosData.sort((a,b) => new Date(a.date_type) > new Date(b.date_type) ? 1 : -1);
    }
  },
  watch: {
    PhotosData(v) {
      if (v == null) return;

    }
  },
  methods: {
    further() {
      this.$emit('further');
    },
    getImgUrl(img) {
      if (img == '') return null;

      const images = require.context('../assets/', false, /\.jpg$/);
      return images('./' + img + ".jpg");
    }
  }
}
</script>

<style scoped>

.card {
  background-color: #eeeeff;
  font-family: 'Arimo', sans-serif;
}


</style>